import { Component, OnInit, ViewChild } from '@angular/core';
import { environment } from '@env/environment';
import { ApiRequestService, Method } from '@app/core/http/api-request.service';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-import',
  templateUrl: './import.component.html',
  styleUrls: ['./import.component.scss'],
})
export class ImportComponent implements OnInit {
  @ViewChild('myPond') myPond: any;
  btnDisable = true;
  matTooltipText;
  pondOptions = {
    class: 'my-filepond',
    multiple: false,
    labelIdle: 'Drop files here',
    acceptedFileTypes: 'application/vnd.ms-excel,text/comma-separated-values, text/csv, application/csv , .csv',
    server: {
      url: environment.serverUrl,
      process: (fieldName, file, metadata, load, error, progress, abort) => {
        const formData = new FormData();
        formData.append('file', file, file.name);
        const request = new XMLHttpRequest();
        request.open('POST', environment.serverUrl + environment.services.assetsUpload());
        request.upload.onprogress = (e) => {
          progress(e.lengthComputable, e.loaded, e.total);
          this.btnDisable = false;
        };
        request.onload = function () {
          if (request.status >= 200 && request.status < 300) {
            load(request.responseText);
            sessionStorage.setItem('uploadFile', request.response);
          } else {
            error('oh no');
          }
        };
        request.send(formData);
        return {
          abort: () => {
            request.abort();
            abort();
          },
        };
      },
      revert: (uniqueFileId, load, error) => {
        error(error);
        load();
      },
      load: (uniqueFileId, load, error) => {
        fetch(uniqueFileId)
          .then((res) => res.blob())
          .then((res) => {
            load(new File([res], '', { type: 'image' }));
          })
          .catch(error);
      },
    },
  };

  constructor(public http: ApiRequestService, private snackBar: MatSnackBar) {
    if (localStorage.getItem('language') === 'Trke') {
      (this.pondOptions.labelIdle = 'Dosyalar buraya srkleyebilirsiniz.'),
        (this.matTooltipText =
          'Burada rnlerinizi bir csv dosyasndan ie aktarabilirsiniz. Ltfen formatn rneimizi karladndan emin olun. Bunun yalnzca rnler ve rn ayrntlar verileri iin olduunu unutmayn. rn reetesi gibi dier rnle ilgili veriler farkl bir ie aktarmay gerektirir.');
    } else {
      this.matTooltipText =
        'Here you can import your products from a csv file. Please make sure the format meets our example. Note that this is for products and product details data only. Other product related data like BOM require a different import.';
    }
  }
  importFile() {
    const data = JSON.parse(sessionStorage.getItem('uploadFile'));
    this.http.callService(new Method(environment.services.uploadCSV(), data, 'post')).subscribe((res) => {
      if (res === 100) {
        this.snackBar.open('file upload successful', '', {
          duration: 5000,
          panelClass: ['success-snackBar'],
        });
      } else {
        this.snackBar.open('file upload failed', '', {
          duration: 5000,
          panelClass: ['error-snackBar'],
        });
      }
    });
  }
  ngOnInit() {}
}
